import { CASHIER_EXTRAS_PERMISSIONS } from "@/common/utils";
export const permissionsHandler = {
  computed: {
    loggedUser() {
      return this.$store.state.user || {};
    },
    isReady() {
      return this.loggedUser && Object.keys(this.loggedUser).length > 0;
    },
    permissions() {
      return this.isReady && this.loggedUser.role.permissions;
    },
    routePathString() {
      return this.$store.state.routes[this.$route.name] || "";
    },
    isCachier() {
      if (this.userRole == "ADMINISTRATOR" || !this.loggedUser.role_id) {
        return true;
      }
      const userPermissions = this.permissions && this.permissions.front_office;
      if (userPermissions) {
        return (
          userPermissions.place_orders.accessible &&
          userPermissions.waiter_orders.accessible &&
          userPermissions.waiter_orders.access.priviledges.every((e) =>
            ["C", "R", "U", "D"].includes(e)
          ) &&
          CASHIER_EXTRAS_PERMISSIONS.some((x) => {
            const obj = userPermissions.waiter_orders.access.others;
            return obj[x] && obj[x] == true;
          })
        );
      }
      return false;
    },
  },
  methods: {
    hasPermissionTo(permission) {
      /**
       * permission ==> CRUD
       */
      if (!this.loggedUser.role_id) {
        return true;
      }
      const group =
        location.pathname.indexOf("/front-office/") != -1
          ? "front_office"
          : "back_office";
      const access = this.routePathString
        .split(".")
        .reduce((a, b) => (a && a[b]) || null, this.permissions[group]);
      return access && access.includes(permission);
    },
  },
};
