<script>
import BootstrapModal from "@/components/BootstrapModal.vue";
import ContentHeader from "@/index/shared/ContentHeader.vue";
export default {
  name: "Categories",
  components: { BootstrapModal, ContentHeader },
  data: () => ({
    newCategory: {
      name: null,
      type: "KITCHEN",
      description: null,
      is_addon: 0,
      image: null,
    },
    categories: [],
    searchKey: null,
  }),
  created() {
    this.$http.get(`items/types`).then((response) => {
      if (response.data.status) {
        this.categories = response.data.rows;
      }
    });
  },
  computed: {
    filteredCategories: function () {
      let searchKey = this.searchKey && this.searchKey.toLowerCase();
      let categories = this.categories;
      if (searchKey) {
        categories = categories.filter((row) => {
          return Object.keys(row).some((key) => {
            return String(row[key]).toLowerCase().indexOf(searchKey) > -1;
          });
        });
      }
      return categories;
    },
  },
  methods: {
    setCategory() {
      this.$http
        .post(
          `items/types/store`,
          this.$helper.generateFormData(this.newCategory)
        )
        .then((response) => {
          if (this.newCategory.id) {
            let index = this.categories.findIndex(
              (item) => (item.id = this.newCategory.id)
            );
            this.categories[index] = response.data.category;
          } else this.categories.unshift(response.data.category);
          this.toggleModal();
        });
    },
    editCategory(category, i) {
      this.editIndex = i;
      this.newCategory = {
        id: category.id,
        name: category.name,
        description: category.description,
      };
      this.toggleModal();
    },
  },
};
</script>
